.materiContainer {
    padding: 100px 50px 0;
}

.materiContainer .title {
    color: #fff;
    padding: 30px 0;
    font-size: 48px;
    text-transform: uppercase;
    letter-spacing: 8px;
}

.materiContainer .content {
	/*background: pink;*/
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}

.materiCardContainer {
	background-color: rgba(255, 255, 255, 0.3);
	border-radius: 8px;
    -webkit-box-shadow: 3px 3px 20px 2px rgba(0, 0, 0, 0.15);
            box-shadow: 3px 3px 20px 2px rgba(0, 0, 0, 0.15);
    width: 280px;
	padding: 40px;
	margin: 40px 0;
	color: #fff;
	letter-spacing: 2px;
}



