::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: #F5F5F5;
}

::-webkit-scrollbar {
  width: 6px;
  background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb {
  background-color: #000000;
}

* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Oxygen', sans-serif;
  background-image: -webkit-linear-gradient(45deg, #F979F7, #7071B9);
  background-image: -o-linear-gradient(45deg, #F979F7, #7071B9);
  background-image: linear-gradient(45deg, #F979F7, #7071B9);
  background-attachment: fixed;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
}

a {
    text-decoration: none;
    color: #000;
}

.section {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}



