.aboutusContainer {
	padding: 50px 50px 0;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.aboutusCardContainer {
    background-color: rgba(255, 255, 255, 0.3);
    -webkit-box-shadow: 3px 3px 20px 2px rgba(0, 0, 0, 0.15);
            box-shadow: 3px 3px 20px 2px rgba(0, 0, 0, 0.15);
    border-radius: 8px;
    width: 550px;
    height: 300px;
    -webkit-transition: all .6s ease;
    -o-transition: all .6s ease;
    transition: all .6s ease;
    opacity: 1;
}

.aboutusCardContainer * {
    padding: 30px;
    color: white;
    text-align: center;
    letter-spacing: 2px;
}

.aboutusTitleContent {
    border-bottom: 1px solid rgba(255, 255, 255, 0.25);
    font-size: 22px;
}

.aboutusCardContainer.hide {
    opacity: 0;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
    -webkit-transition-delay: -.3s;
         -o-transition-delay: -.3s;
            transition-delay: -.3s;
}

.aboutusDescriptionContent {
    /*background-color: pink;*/
}

.aboutusContentNavigation {
    position: fixed;
    top: 50%;
    right: 0;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    margin-right: 70px;
}

.aboutusContentNavigation ul {
    list-style: none;
    padding: 0;
    margin: 50px 0 0;
    text-align: right;
}

.aboutusContentNavigation ul li {
    margin-bottom: 5px;
}

.aboutusContentNavigation ul li button {
    color: #fff;
    display: inline-block;
    padding: 10px 0 10px 10px;
    position: relative;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    cursor: pointer;
    outline: none;
}

.aboutusContentNavigation ul li button span::before {
    content: '';
    position: absolute;
    left: 100%;
    right: 0%;
    bottom: -3px;
    display: block;
    height: 3px;
    background-color: #fff;
    -webkit-transition: 0.3s ease;
    -o-transition: 0.3s ease;
    transition: 0.3s ease;
}

.aboutusContentNavigation ul .active span::before,
.aboutusContentNavigation ul li button:hover span::before {
    left: 0%;
}



