.homeContainer {
    padding: 100px 50px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.leftContent {
    display: block;
    width: 660px;
    height: 420px;
    background-color: blue;
}

.rightContent {
    max-width: 500px;
    text-align: right;
    font-weight: lighter;
}

.rightContent .title {
    font-size: 72px;
    color: #B0AAFF;
    text-transform: uppercase;
}

.rightContent .description {
    font-size: 20px;
    color: #E2E0FF;
    margin-top: 50px;
    line-height: 1.4;
    letter-spacing: 1.5px;
}



